@import '../../defaultStyles.scss';

.card {
    display: flex;
    flex-direction: column;
    max-width: 26%;
    height: auto;
    max-height: 515px;

    object-fit: cover;
    object-position: bottom;

    text-align: left;
    margin-left: 3.5%;
    margin-bottom: 3.5%;
    text-decoration: none;

    &:hover {
        transition: 0.3s ease-in;
        opacity: 0.7;
        cursor: pointer;
    }

    &__image {
        width: 100%;
        // height: 70%;
        border-radius: 12px;
        background-color: $accentColor;
    }

    &__name {
        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr;
        font-weight: 500;
        margin-top: 3%;
        white-space: nowrap; /* Запрещает перенос текста на новую строку */
        overflow: hidden; /* Скрывает часть текста, выходящую за пределы контейнера */
        text-overflow: ellipsis; /* Добавляет многоточие (...) к тексту, который не помещается */
    }

    &__price {
        color: $mediumColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr;
        font-weight: 500;
        margin-top: 2%;
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .card {
        max-height: 515px;

        &__name {
            font-size: $fontSizeSmallDescr - 0.2em;
        }

        &__price {
            font-size: $fontSizeSmallDescr - 0.2em;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .card {
        max-height: 515px;

        &__name {
            font-size: $fontSizeSmallDescr - 0.2em;
        }

        &__price {
            font-size: $fontSizeSmallDescr - 0.2em;
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .card {
        max-height: 515px;

        &__name {
            font-size: $fontSizeSmallDescr - 0.45em;
        }

        &__price {
            font-size: $fontSizeSmallDescr - 0.45em;
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .card {
        max-height: 515px;
    
        &__name {
            font-size: $fontSizeSmallDescr - 0.75em;
        }
    
        &__price {
            font-size: $fontSizeSmallDescr  - 0.75em;
        }
    }
}