@import '../../defaultStyles.scss';

.header {

    &__list {
        width: 100%;
        list-style: none;
        display: flex;
        padding: 2% 0;
        flex-direction: row;
        align-items: center;

        &__item {

            &:not(:first-child) {
                margin-left: 3%;
            }

            &__link {
                text-decoration: none;
                color: $mediumColor;
                font-family: "Poppins", sans-serif;
                font-size: 1.3em;
                font-weight: 500;

                &:hover {
                    color: $darkColor;
                    transition: 0.5s;
                }

                img {
                    margin-right: 95px;

                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {}

@media (min-width: 768px) and (max-width: 1023px) {}

@media (min-width: 576px) and (max-width: 767px) {
    .header {
        &__list {
            justify-content: space-between;

            &__item {

                &:not(:first-child) {
                    margin-left: 5%;
                }
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .header {

        &__list {
            flex-direction: column;

            &__item {

                &:not(:first-child) {
                    margin: 2% 0;
                }

                &__link {

                    img {
                        margin: 20% 0;
                    }
                }

            }
        }
    }
}