@import '../../defaultStyles.scss';

.categoryBox {
    width: 100%;
    margin: 7% 0;

    &__btn {
        border: none;
        background: none;
        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr;
        font-weight: 600;
        background-image: url('../../imgs/arrowDown.svg');
        background-position: left center;
        background-repeat: no-repeat;
        width: 100%;
        text-align: left;
        padding-left: 15%;

        &:hover {
            cursor: pointer;
        }
    }

    &__list {
        list-style: none;
        padding: 0;
        margin: 0;

        &__item {
            margin: 7% 0;
            color: $mediumColor;
            font-family: $font;
            font-size: $fontSizeSmallDescr;
            font-weight: 500;
        }
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .categoryBox {

        &__btn {
            font-size: $fontSizeSmallDescr - 0.2em;
        }

        &__list {

            &__item {
                margin: 5% 0;
                font-size: $fontSizeSmallDescr - 0.3em;
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .categoryBox {
        margin: 5% 0;

        &__btn {
            padding-left: 15%;
            font-size: $fontSizeSmallDescr - 0.4em;
        }

        &__list {

            &__item {
                margin: 4% 0;
                font-size: $fontSizeSmallDescr - 0.4em;
            }
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .categoryBox {
        margin: 5% 0;

        &__btn {
            padding-left: 15%;
            font-size: $fontSizeSmallDescr - 0.6em;
            background-size: 8%;
        }

        &__list {

            &__item {
                margin: 4% 0;
                font-size: $fontSizeSmallDescr - 0.7em;
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .categoryBox {
        margin: 5% 0;

        &__btn {
            padding-left: 15%;
            font-size: $fontSizeSmallDescr - 0.7em;
            background-size: 7%;
        }

        &__list {

            &__item {
                margin: 4% 0;
                font-size: $fontSizeSmallDescr - 0.7em;
            }
        }
    }
}