@import '../../defaultStyles.scss';

.categoryBlock {
    width: 100%;
    text-align: center;
    justify-content: center;
    padding-bottom: 10%;


    &__title {
        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeTitle;
        font-weight: 600;
    }

    &__descr {
        margin-top: 1.5%;

        color: $mediumColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr;
        font-weight: 500;
    }

    &__items {
        display: flex;
        justify-content: space-between;
        margin-top: 4%;
        margin-bottom: 7%;

        &:nth-child(2) {
            margin-top: 5%;
        }

    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .categoryBlock {

        &__title {
            font-size: $fontSizeSubTitle;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.2em;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .categoryBlock {

        &__title {
            font-size: $fontSizeSubTitle - 0.4em;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.4em;
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .categoryBlock {

        &__title {
            font-size: $fontSizeSmallDescr;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.6em;
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .categoryBlock {

        &__title {
            font-size: $fontSizeSmallDescr - 0.4em;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.7em;
        }
    }
}