@import '../../defaultStyles.scss';

.categoryItem {
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:hover {
        transition: 0.3s ease-in;
        opacity: 0.8;
    }

    &__image {
        height: auto;
        max-height: 515px;
        max-width: 90%;
        border-radius: 12px;
        background-color: $accentLightColor;

        &:hover {
            transition: 0.5s;
        }
    }

    &__title {
        margin-top: 5%;
        max-width: 90%;

        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr;
        font-weight: 600;
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .categoryItem {
        &__title {
            font-size: $fontSizeSmallDescr - 0.2em;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .categoryItem {
        &__title {
            font-size: $fontSizeSmallDescr - 0.4em;
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .categoryItem {
        &__title {
            font-size: $fontSizeSmallDescr - 0.6em;
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .categoryItem {
        &__title {
            font-size: $fontSizeSmallDescr - 0.8em;
        }
    }
}