@import '../../defaultStyles.scss';

.catalogPage {

    &__mane {
        margin: 3% 0;
        padding: 3% 0;
        border-top: 1.5px solid $darkColor;
        display: flex;

        &__categories {
            width: 30%;
        }


        &__products {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: start;

            &__card {
                text-decoration: none;
            }

        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .catalogPage {

        &__mane {

            &__categories {
                width: 40%;
            }


            &__products {

                .card {
                    max-width: 40%;
                    margin-bottom: 5%;
                }

            }
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .catalogPage {

        &__mane {

            &__categories {
                width: 40%;
            }


            &__products {

                .card {
                    max-width: 40%;
                    margin-bottom: 5%;
                }

            }
        }
    }
}