@import '../../defaultStyles.scss';

.contactsPage {

    &__title {
        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeTitle;
        font-weight: 600;
        margin-bottom: 7%;
    }

    &__mane {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__mapBlock {
        position: relative;
        overflow: hidden;
        display: flex;
        justify-content: center;

        width: 100%;
        margin-bottom: 10%;

        &__a1 {
            color: $darkColor;
            font-size: $fontSizeSmallDescr;
            position: absolute;
            top: 0px;
        }

        &__a2 {
            color: $darkColor;
            font-size: $fontSizeSmallDescr;
            position: absolute;
            top: 14px;
        }

        &__iframe {
            border: none;
            position: relative;
            border-radius: 12px;
            width: 100%;
            height: 400px;
            max-height: 500px;
        }
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .contactsPage {

        &__title {
            font-size: $fontSizeSubTitle - 0.2em;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .contactsPage {

        &__title {
            font-size: $fontSizeSubTitle - 0.2em;
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .contactsPage {

        &__title {
            font-size: $fontSizeSubTitle - 0.4em;
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .contactsPage {

        &__title {
            font-size: $fontSizeSubTitle - 0.6em;
        }
    }
}