@import '../../defaultStyles.scss';

.btn {
    background: url(../../imgs/arrow.svg);
    background-position: 90% center;
    background-repeat: no-repeat;
    background-size: 12%;
    background-color: $accentColor;
    border: none;

    border-radius: 8px;

    color: $darkColor;
    font-family: $font;
    font-size: $fontSizeSmallDescr;
    font-weight: 500;
    font-size: 1em;

    text-align: left;

    padding: 12px 64px 12px 24px;

    &:hover {
        transition: 0.5s;
        cursor: pointer;
        background-color: $accentLightColor;
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .btn {
        font-size: $fontSizeSmallDescr - 0.3em;
        padding: 8px 52px 8px 16px;
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .btn {
        font-size: $fontSizeSmallDescr - 0.4em;
        padding: 8px 52px 8px 16px;
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .btn {
        font-size: $fontSizeSmallDescr - 0.5em;
        padding: 6px 42px 6px 12px;
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .btn {
        font-size: $fontSizeSmallDescr - 0.8em;
        padding: 3px 30px 3px 8px;
    }
}