@import '../../defaultStyles.scss';

.productItem {
    margin-top: 5%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    text-align: left;
    padding-bottom: 10%;

    &__image {
        height: auto;
        max-height: 615px;
        max-width: 65%;
        border-radius: 12px;
        margin-right: 4%;
    }

    &__name {
        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeTitle;
        font-weight: 600;

        margin-bottom: 3.5%;
    }

    &__category_title,
    &__category_value,
    &__author_title,
    &__author_value,
    &__size_title,
    &__size_value,
    &__descr_title,
    &__descr_value {
        color: $mediumColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr - 0.15em;
        font-weight: 500;
        margin-bottom: 1%;
    }

    &__category_value,
    &__author_value,
    &__size_value,
    &__descr_value {
        margin-bottom: 1%;
        font-weight: 500;
        font-size: $fontSizeDescr - 0.2em;
    }

    &__buy {
        margin-top: 10%;
        justify-content: space-between;
        display: flex;

        &__price {

            &__title,
            &__value {
                color: $mediumColor;
                font-family: $font;
                font-size: $fontSizeSmallDescr;
                font-weight: 500;
                margin-bottom: 4%;
            }

            &__value {
                font-size: $fontSizeDescr;
            }
        }
    }

}

.flex {
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .productItem {
        display: flex;
        justify-content: space-between;

        &__image {
            height: auto;
            max-height: 515px;
            max-width: 45%;
            margin-right: 4%;
        }

        &__name {
            font-size: $fontSizeTitle - 0.2em;
            margin-bottom: 3.5%;
        }

        &__category_title,
        &__category_value,
        &__author_title,
        &__author_value,
        &__size_title,
        &__size_value,
        &__descr_title,
        &__descr_value {
            font-size: $fontSizeSmallDescr - 0.25em;
        }

        &__category_value,
        &__author_value,
        &__size_value,
        &__descr_value {
            font-size: $fontSizeDescr - 0.3em;
        }

        &__buy {
            margin-top: 10%;
            justify-content: space-between;
            display: flex;

            &__price {

                &__title,
                &__value {
                    font-size: $fontSizeSmallDescr - 0.2em;
                    margin-bottom: 4%;
                }

                &__value {
                    font-size: $fontSizeDescr - 0.2em;
                }
            }
        }

    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .productItem {
        display: flex;
        justify-content: space-between;

        &__image {
            height: auto;
            max-height: 350px;
            max-width: 45%;
            margin-right: 4%;
        }

        &__name {
            font-size: $fontSizeTitle - 0.4em;
            margin-bottom: 3.5%;
        }

        &__category_title,
        &__category_value,
        &__author_title,
        &__author_value,
        &__size_title,
        &__size_value,
        &__descr_title,
        &__descr_value {
            font-size: $fontSizeSmallDescr - 0.45em;
        }

        &__category_value,
        &__author_value,
        &__size_value,
        &__descr_value {
            font-size: $fontSizeDescr - 0.6em;
        }

        &__buy {
            margin-top: 10%;
            justify-content: space-between;
            display: flex;

            &__price {

                &__title,
                &__value {
                    font-size: $fontSizeSmallDescr - 0.4em;
                    margin-bottom: 4%;
                }

                &__value {
                    font-size: $fontSizeDescr - 0.4em;
                }
            }
        }

    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .productItem {
        display: flex;
        justify-content: space-between;

        &__image {
            height: auto;
            max-height: 250px;
            max-width: 45%;
            margin-right: 2%;
        }

        &__name {
            font-size: $fontSizeTitle - 0.7em;
            margin-bottom: 3%;
        }

        &__category_title,
        &__category_value,
        &__author_title,
        &__author_value,
        &__size_title,
        &__size_value,
        &__descr_title,
        &__descr_value {
            font-size: $fontSizeSmallDescr - 0.5em;
        }

        &__category_value,
        &__author_value,
        &__size_value,
        &__descr_value {
            font-size: $fontSizeDescr - 0.65em;
        }

        &__buy {
            margin-top: 10%;
            justify-content: space-between;
            display: flex;

            &__price {

                &__title,
                &__value {
                    font-size: $fontSizeSmallDescr - 0.5em;
                    margin-bottom: 4%;
                }

                &__value {
                    font-size: $fontSizeDescr - 0.5em;
                }
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .productItem {
        display: flex;
        flex-wrap: wrap;

        &__image {
            height: auto;
            max-height: 400px;
            max-width: 100%;
            margin-right: 2%;
        }

        &__name {
            margin-top: 5%;
            font-size: $fontSizeTitle - 1em;
            margin-bottom: 3%;
        }

        &__category_title,
        &__category_value,
        &__author_title,
        &__author_value,
        &__size_title,
        &__size_value,
        &__descr_title,
        &__descr_value {
            font-size: $fontSizeSmallDescr - 0.6em;
        }

        &__category_value,
        &__author_value,
        &__size_value,
        &__descr_value {
            font-size: $fontSizeDescr - 0.7em;
        }

        &__buy {
            margin-top: 10%;
            justify-content: space-between;
            display: flex;

            &__price {

                &__title,
                &__value {
                    font-size: $fontSizeSmallDescr - 0.6em;
                    margin-bottom: 4%;
                }

                &__value {
                    font-size: $fontSizeDescr - 0.6em;
                }
            }
        }
    }

    .flex {
        width: 100%;
    }
}