@import '../../defaultStyles.scss';

.contactItem {
    display: flex;
    flex-direction: row;
    padding: 0 1%;
    width: 30%;
    align-items: center;

    &__avatar {
        max-width: 30%;
        height: auto;
        max-height: 80px;
        margin-right: 10%;
        border-radius: 50%;
        text-align: center;
    }

    &__info {
        text-align: left;
        align-items: center;

        &__descr {
            color: $mediumColor;
            font-family: $font;
            font-size: $fontSizeSmallDescr - 0.3em;
            font-weight: 600;
            margin-bottom: 5%;
        }

        &__title {
            color: $mediumColor;
            font-family: $font;
            font-size: $fontSizeSmallDescr - 0.3em;
            font-weight: 500;
        }
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .contactItem {

        &__avatar {
            width: 30%;
            margin-right: 10%;
        }

        &__info {

            &__descr {
                font-size: $fontSizeSmallDescr - 0.3em;
                margin-bottom: 5%;
            }

            &__title {
                font-size: $fontSizeSmallDescr - 0.3em;
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .contactItem {

        &__avatar {
            width: 30%;
            margin-right: 5%;
        }

        &__info {

            &__descr {
                width: 80%;
                font-size: $fontSizeSmallDescr - 0.6em;
                margin-bottom: 5%;
            }

            &__title {
                font-size: $fontSizeSmallDescr - 0.6em;
            }
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .contactItem {

        &__avatar {
            width: 30%;
            margin-right: 5%;
        }

        &__info {

            &__descr {
                font-size: $fontSizeSmallDescr - 0.7em;
                margin-bottom: 5%;
            }

            &__title {
                font-size: $fontSizeSmallDescr - 0.7em;
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .contactItem {

        &__avatar {
            width: 30%;
            margin-right: 5%;
            visibility: hidden;
        }

        &__info {

            &__descr {
                font-size: $fontSizeSmallDescr - 0.8em;
                margin-bottom: 5%;
            }

            &__title {
                font-size: $fontSizeSmallDescr - 0.8em;
            }
        }
    }
}