@import '../../defaultStyles.scss';

.previewBlock {
    width: 100%;
    height: 500px;
    background-color: $accentColor;
    background-image: url('../../imgs/hand.svg');
    background-position: right bottom;
    background-size: 35%;
    background-repeat: no-repeat;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    padding: 3%;

    &__title {
        font-family: $font;
        font-size: $fontSizeBigTitle;
        font-weight: 700;
        color: $darkColor;
        width: 55%;
        margin-left: 3%;
        line-height: 115%;
    }

    &__positions {
        margin-left: 3%;
        margin-top: 3%;
        display: flex;

        &__item {
            text-align: left;
            width: 15%;


            &:first-child {
                border-right: 1.5px solid $darkColor;
                padding-right: 18%;
            }

            &:last-child {
                margin-left: 8%;
            }

            &__count {
                font-family: $font;
                font-size: $fontSizeTitle;
                font-weight: 500;
                color: $darkColor;
            }

            &__name {
                font-family: $font;
                font-size: $fontSizeDescr;
                font-weight: 500;
                color: $darkColor;
            }
        }
    }
}


@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .previewBlock {
        height: 450px;
        background-size: 40%;

        &__positions {
            &__item {

                &:first-child {
                    padding-right: 25%;
                }

                &__count {
                    font-size: $fontSizeDescr;
                }
            }
        }

    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .previewBlock {
        height: 350px;
        background-size: 40%;

        &__title {
            font-size: $fontSizeTitle;
        }

        &__positions {
            &__item {

                &:first-child {
                    padding-right: 30%;
                }

                &__count {
                    font-size: $fontSizeDescr;
                }

                &__name {
                    font-size: $fontSizeDescr;
                }
            }
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .previewBlock {
        height: 250px;
        background-size: 40%;


        &__title {
            font-size: $fontSizeSubTitle + 0.5em;
        }

        &__positions {
            &__item {

                &:first-child {
                    padding-right: 30%;
                }

                &__count {
                    font-size: $fontSizeDescr - 0.3em;
                }

                &__name {
                    font-size: $fontSizeDescr - 0.3em;
                }
            }
        }
    }

}

@media (min-width: 320px) and (max-width: 575px) {
    .previewBlock {
        height: 200px;
        background-size: 40%;

        &__title {
            font-size: $fontSizeDescr;
            width: 100%;
        }

        &__positions {
            width: 100%;
            flex-direction: column;

            &__item {
                &:last-child {
                    margin-left: 0%;
                    margin-top: 3%;
                }

                &:first-child {
                    border: none;
                    padding-right: 30%;
                }

                &__count {
                    font-size: $fontSizeDescr - 0.5em;
                }

                &__name {
                    font-size: $fontSizeDescr  - 0.5em;
                }
            }
        }
    }
}