@import '../../defaultStyles.scss';

.lastWorksBlock {
    display: flex;
    margin-top: 6%;
    justify-content: space-between;
    width: 100%;

    &__descr {
        text-align: left;
        width: 20%;

        &__title {
            color: $darkColor;
            font-family: $font;
            font-size: $fontSizeTitle;
            font-weight: 700;
            line-height: 140%;
        }

        &__subtitle {
            color: $mediumColor;
            font-family: $font;
            font-size: $fontSizeDescr - 0.3em;
            font-weight: 500;
            margin-top: 5%;
            margin-bottom: 10%;
        }
    }

    &__cards {
        display: flex;
        flex-direction: row-reverse;
        width: 85%;
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {

    .lastWorksBlock {

        &__descr {

            &__title {
                font-size: $fontSizeSubTitle;
            }

            &__subtitle {
                font-size: $fontSizeSmallDescr;
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .lastWorksBlock {

        &__descr {

            &__title {
                font-size: $fontSizeDescr;
            }

            &__subtitle {
                font-size: $fontSizeSmallDescr - 0.4em;
            }
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .lastWorksBlock {

        &__descr {

            &__title {
                font-size: $fontSizeDescr - 0.2em;
            }

            &__subtitle {
                font-size: $fontSizeSmallDescr - 0.6em;
            }
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .lastWorksBlock {

        &__descr {

            &__title {
                font-size: $fontSizeDescr - 0.7em;
            }

            &__subtitle {
                font-size: $fontSizeSmallDescr - 0.8em;
            }
        }
    }
}