@import '../../defaultStyles.scss';


.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;

    &__content {
        background-color: white;
        padding: 1.5%;
        border-radius: 10px;
        width: 70%;
        height: 90%;

        &__product {
            align-items: center;
            margin-top: 2%;
            padding-bottom: 2%;
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: center;
            border-bottom: 5px solid $mediumColor;

            &__descr {
                margin-left: 2%;
                color: $darkColor;
                font-family: $font;
                font-size: $fontSizeSmallDescr;
            }

            &__image {
                width: 5%;
                border-radius: 8px;
            }
        }

        &__title {
            color: $darkColor;
            font-family: $font;
            font-size: $fontSizeTitle;
            text-align: center;
        }

        &__form {
            padding: 2%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;


            &__contactInfo {
                display: flex;
                flex-direction: column;
                width: 45%;

                &__title {
                    margin-bottom: 2%;
                    color: $darkColor;
                    font-family: $font;
                    font-size: $fontSizeSubTitle;
                }
            }

            &__addressInfo {
                display: flex;
                flex-direction: column;
                width: 45%;

                &__title {
                    margin-bottom: 2%;
                    color: $darkColor;
                    font-family: $font;
                    font-size: $fontSizeSubTitle;
                }
            }

            &__input {
                padding: 2%;
                margin-top: 3%;

                border: 1px solid $darkColor;
                border-radius: 8px;

                color: $darkColor;
                font-family: $font;
                font-size: $fontSizeSmallDescr - 0.2em;

                &:active,
                &:focus {
                    transition: 0.5s;
                    border: 1px solid $accentColor;
                    outline: 0;
                    outline-offset: 0;
                }
            }

            &__errors {
                color: $errorColor;
                font-family: $font;
                font-size: $fontSizeSmallDescr - 0.5em;
                opacity: 0.5;
            }

            &__submitBtn {
                background: $darkColor;
                position: absolute;
                bottom: 8%;
                border-radius: 12px;
                border: none;

                font-size: $fontSizeSmallDescr;
                color: white;
                padding: 1% 2%;
                width: 65%;

                &:hover {
                    cursor: pointer;
                    transition: 0.3s;
                    opacity: 0.85;
                }
            }
        }
    }
}

.close {
    top: 6%;
    right: 16%;
    position: absolute;
    background: transparent;
    border: none;
    font-size: $fontSizeSmallDescr - 0.2em;
    cursor: pointer;
}

.flexAddress {
    margin-top: 3.5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.flexAddress div {
    width: 30%;
}

.flexAddress div input {
    width: 100%;
    padding: 5%;
}


@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {}

