                    }
                }

                &__input {
                    padding: 2%;
                    margin-top: 5%;

                    font-size: $fontSizeSmallDescr - 0.2em;
                }

                &__errors {
                    font-size: $fontSizeSmallDescr - 0.3em;
                }

                &__submitBtn {
                    width: 74%;
                    font-size: $fontSizeSmallDescr - 0.2em;
                    padding: 1% 2%;
                }
            }
        }
    }

    .close {
        top: 6%;
        right: 12%;
        font-size: $fontSizeDescr;
    }

    .flexAddress {
        margin-top: 3.5%;
