@import '../../defaultStyles.scss';

.footer {
    width: 100%;
    padding: 4% 0;
    border-top: 1.5px solid $darkColor;

    &__flex {
        display: flex;
    }

    &__leftBlock {
        width: 40%;
        margin-right: 15%;


        &__logo {}

        &__descr {
            color: $mediumColor;
            font-family: $font;
            font-size: $fontSizeSmallDescr;
            font-weight: 500;
            width: 50%;
            margin-top: 5%;

        }

        &__socials {
            display: flex;
            margin-top: 5%;

            &__item {
                padding: 4%;
                border-radius: 50%;
                border: 1px solid $mediumColor;

                &:hover {
                    transition: 0.5s;
                    background: $accentLightColor;
                }

                &:not(:last-child) {
                    margin-right: 5%;
                }
            }
        }
    }

    &__nav {
        width: 50%;
        display: flex;


        &__list {
            list-style: none;
            margin: 0;
            padding: 0;

            &:not(:last-child) {
                margin-right: 10%;
            }

            &__item {

                &:not(:last-child) {
                    margin-bottom: 35px;
                }

                &__link {
                    text-decoration: none;
                    color: $mediumColor;
                    font-family: $font;
                    font-size: $fontSizeSmallDescr;
                    font-weight: 500;

                    &:hover {
                        transition: 0.5s;
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    &__copy {
        margin-top: 5%;
        color: $mediumColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr - 0.3em;
        font-weight: 500;
    }
}

.mane_link {
    font-weight: 700;
    color: $darkColor;
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .footer {

        &__leftBlock {
            margin-right: 5%;

            &__descr {
                width: 70%;
            }
        }

        &__nav {
            width: 60%;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .footer {

        &__leftBlock {
            margin-right: 0%;

            &__descr {
                font-size: $fontSizeSmallDescr - 0.3em;
                width: 70%;
                margin-top: 5%;

            }

            &__socials {
                margin-top: 5%;

                &__item {
                    padding: 4%;

                    &:not(:last-child) {
                        margin-right: 5%;
                    }
                }
            }
        }

        &__nav {
            width: 50%;
            display: flex;

            &__list {

                &:not(:last-child) {
                    margin-right: 7%;
                }

                &__item {

                    &:not(:last-child) {
                        margin-bottom: 25px;
                    }

                    &__link {
                        font-size: $fontSizeSmallDescr - 0.3em;
                    }
                }
            }
        }

        &__copy {
            margin-top: 5%;
            font-size: $fontSizeSmallDescr - 0.4em;
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .footer {
        &__flex {
            display: block;
        }

        &__leftBlock {
            margin-right: 5%;
            width: 100%;
            margin-right: 5%;
            display: flex;
            align-items: center;
            justify-content: center;

            &__descr {
                font-size: $fontSizeSmallDescr - 0.5em;
                width: 70%;
                margin-top: 0%;
                margin-left: 10%;

            }

            &__socials {
                margin-top: 0%;

                &__item {
                    padding: 4%;

                    &:not(:last-child) {
                        margin-right: 5%;
                    }
                }
            }
        }

        &__nav {
            justify-content: center;
            width: 100%;
            display: flex;
            margin-top: 7%;

            &__list {

                &__item {

                    &:not(:last-child) {
                        margin-bottom: 25px;
                    }

                    &__link {
                        font-size: $fontSizeSmallDescr - 0.4em;
                    }
                }
            }
        }

        &__copy {
            margin-top: 5%;
            font-size: $fontSizeSmallDescr - 0.4em;
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .footer {

        &__flex {
            display: block;
        }

        &__leftBlock {
            margin-right: 5%;
            width: 100%;
            margin-right: 5%;
            display: flex;
            align-items: center;
            justify-content: center;

            &__logo {
                width: 20%;
            }

            &__descr {
                font-size: $fontSizeSmallDescr - 0.7em;
                width: 70%;
                margin-top: 0%;
                margin-left: 6%;
                margin-right: 6%;
                text-align: center;

            }

            &__socials {
                margin-top: 0%;
                width: 15%;

                &__item {
                    padding: 12%;

                    &:not(:last-child) {
                        margin-right: 5%;
                    }
                }
            }
        }

        &__nav {
            justify-content: center;
            width: 100%;
            display: flex;
            margin-top: 7%;


            &__list {

                &:not(:last-child) {
                    margin-right: 10%;
                }

                &__item {

                    &:not(:last-child) {
                        margin-bottom: 10%;
                    }

                    &__link {
                        font-size: $fontSizeSmallDescr - 0.6em;
                    }
                }
            }
        }

        &__copy {
            margin-top: 10%;
            font-size: $fontSizeSmallDescr - 0.8em;
        }
    }
}