@import '../../defaultStyles.scss';

.aboutItem {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    width: 33%;

    &__container {
        padding: 10%;
        background-color: $accentColor;
        border-radius: 50%;
        width: 30%;

        &:hover {
            transition: 0.5s;
            background-color: $accentLightColor;
        }

        &__image {
            width: 100%;
        }
    }

    &__title {
        margin-top: 6%;

        color: $darkColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr;
        font-weight: 600;
    }

    &__descr {
        margin-top: 4%;

        color: $mediumColor;
        font-family: $font;
        font-size: $fontSizeSmallDescr - 0.1em;
        font-weight: 500;
    }
}

@media screen and (min-width: 1278px) {}

@media (min-width: 1024px) and (max-width: 1279px) {
    .aboutItem {

        &__container {
            padding: 10%;
        }

        &__title {
            font-size: $fontSizeSmallDescr - 0.2em;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.3em;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .aboutItem {

        &__container {
            padding: 10%;
        }

        &__title {
            font-size: $fontSizeSmallDescr - 0.2em;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.3em;
        }
    }
}

@media (min-width: 576px) and (max-width: 767px) {
    .aboutItem {

        &__container {
            padding: 10%;
        }

        &__title {
            font-size: $fontSizeSmallDescr - 0.4em;
        }

        &__descr {
            font-size: $fontSizeSmallDescr - 0.5em;
            width: 90%;
        }
    }
}

@media (min-width: 320px) and (max-width: 575px) {
    .aboutItem {

        &__container {
            padding: 5%;
            width: 20%;

            &__image {
                width: 100%;
            }
        }

        &__title {
            margin-top: 3%;
            font-size: $fontSizeSmallDescr - 0.7em;
            text-align: left;
        }

        &__descr {
            margin-top: 2%;
            font-size: $fontSizeSmallDescr - 0.8em;
            width: 90%;
        }
    }
}